export default {
	name: 'i-cell',
	props: {
		icon: String,
		iconColor: {
			type: String,
			default: '#333'
		},
		value: String,
		arrow: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		handle() {
			this.$emit('click');
		}
	}
};
